/*
0 - 600px   Phone
600 - 900px     Tablet portrait
900 - 1200px    Tablet landscape
1200 - 1800px   Normal styles applied
1800px+    Big Desktop
*/
@media (max-width: 52.25em) {
  .header {
    position: fixed;
    width: 100%;
    background-color: white;
  }
}
.header__logo {
  position: relative;
  z-index: 2001;
  transition: position 0.5s ease;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.25rem;
}

.body {
  height: 180rem;
  background-color: bisque;
}

.navigation__checkbox {
  position: absolute;
  appearance: none;
  z-index: -1;
}
.navigation__button {
  display: none;
  background-color: transparent;
  height: 1.875rem;
  width: 1.875rem;
  z-index: 2000;
  cursor: pointer;
  transition: all 0.8s;
}
@media (max-width: 52.25em) {
  .navigation__button {
    position: fixed;
    display: block;
    top: 2.1875rem;
    right: 1.5625rem;
  }
}
@media (max-width: 52.25em) {
  .navigation__icon {
    position: relative;
    margin-top: 0.875rem;
  }
  .navigation__icon, .navigation__icon::before, .navigation__icon::after {
    width: 1.5625rem;
    height: 2px;
    background-color: var(--body);
    display: inline-block;
    transition: all 0.2s;
  }
  .navigation__icon::before, .navigation__icon::after {
    content: "";
    position: absolute;
    left: 0;
  }
  .navigation__icon::before {
    top: -0.625rem;
  }
  .navigation__icon::after {
    top: 0.625rem;
  }
}
.navigation__button:hover .navigation__icon::before {
  top: -0.6875rem;
}
.navigation__button:hover .navigation__icon::after {
  top: 0.6875rem;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}
.navigation__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 52.25em) {
  .navigation__nav {
    display: none;
  }
}
.navigation__list {
  font-size: 1.25rem;
  display: flex;
  gap: 1.25rem;
  list-style: none;
  text-align: center;
  width: 100%;
}
@media (max-width: 52.25em) {
  .navigation__list {
    display: block;
  }
}
@media (max-width: 52.25em) {
  .navigation__item {
    display: none;
  }
}
.navigation__link {
  color: var(--body);
  text-decoration: none;
  text-transform: uppercase;
}
.navigation__link--home.active {
  color: var(--primary);
}
.navigation__socials {
  display: none;
}
.navigation__socials__item {
  height: 1.5rem;
  width: 1.5rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
}
.navigation__socials__item::before {
  content: "";
  height: 4rem;
  width: 4rem;
  background-color: var(--grey1);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.navigation__socials__item--facebook {
  background-image: url(../../../images/FB.png);
}
.navigation__socials__item--twitter {
  background-image: url(../../../images/TW.png);
}
.navigation__socials__item--instagram {
  background-image: url(../../../images/INST.png);
}
.navigation__socials__item--twitter2 {
  background-image: url(../../../images/TW.png);
}
.navigation__checkbox:checked ~ .navigation__background {
  height: 100%;
  transition: height 0.5s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 40%;
  transform: translate(0, -50%);
  left: 0.125rem;
  background-color: transparent;
  z-index: 1000;
  animation: moveNavInLeft 0.8s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__nav .navigation__list {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-evenly;
}
.navigation__checkbox:checked ~ .navigation__nav .navigation__item {
  display: inline-block;
  margin: 1.25rem;
  animation: fadeInUp 0.5s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__socials {
  display: block;
  position: fixed;
  bottom: 5rem;
  left: 0.125rem;
  z-index: 1000;
  width: 100%;
  animation: moveInBottom 0.8s ease-in-out 0.5s;
  animation-fill-mode: backwards;
}
.navigation__checkbox:checked ~ .navigation__socials > .navigation__socials__container {
  display: flex;
  justify-content: space-around;
}
.navigation__checkbox:checked ~ .navigation__socials > .navigation__socials__container a {
  border-radius: 50%;
  padding: 2rem;
}
.navigation__background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: var(--white);
  z-index: 100;
  transition: all 0.7s ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-default);
  font-size: 1.125rem;
  color: var(--body);
}

h1 {
  font-size: 4.375rem;
  line-height: 5.25rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 768px) {
  h1 {
    font-size: 3.375rem;
    line-height: 4rem;
  }
}
h2 {
  font-size: 3rem;
  line-height: 3.5rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 768px) {
  h2 {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}
h3 {
  font-size: 2.5rem;
  line-height: 3rem;
  letter-spacing: 0.36px;
}

h4 {
  font-size: 1.875rem;
  line-height: 2rem;
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --white: #fff;
  --grey1: #f7f7f7;
  --grey2: #f1f1f1;
  --grey3: #d4d4d4;
  --grey4: #b8b8b8;
  --grey5: #525c60;
  --primary: #7eb693;
  --secondary: #ffa858;
  --body: #274c5b;
}

:root {
  --font-default: "Roboto", sans-serif;
  --font-display: "Yellowtail", cursive;
}

@keyframes moveNavInLeft {
  0% {
    opacity: 0;
    transform: translate(-10rem, -50%);
  }
  80% {
    transform: translate(1rem, -50%);
  }
  100% {
    opacity: 1;
    transform: translate(0, -50%);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}